import $ from "jquery";
import { tns } from "../../node_modules/tiny-slider/src/tiny-slider";

export default class TwitterbarSlider {
  constructor($wrapper) {
    this.$wrapper = $wrapper;
    this.$slider = $wrapper.find(".twitter-posts");
    //this.$pager = $wrapper.find('.slideshow-pager');

    this.initSlider();
    this.bindEvents();
  }

  initSlider() {
    //const mode = 768 <= $(window).width() ? "gallery" : "carousel";
    const mode = "gallery";

    this.slider = tns({
      container: this.$slider.get(0),
      items: 1,
      nav: false,
      lazyload: true,
      mode: mode,
      speed: 100,
      autoHeight: true,
      autoplay: true,
    });

    this.onSlideChange();
  }

  bindEvents() {
    this.slider.events.on("indexChanged", this.onSlideChange.bind(this));
  }

  onSlideChange() {
    const slider_info = this.slider.getInfo();
    const $slides = this.$wrapper.find(".twitter-post");

    const max_pagers = Math.min(
      slider_info.slideCount,
      768 <= $(window).width() ? 5 : 3
    );
    const max_pagers_half = Math.floor(max_pagers / 2);
    const slider_idx =
      slider_info.slideCount < slider_info.displayIndex
        ? 0
        : slider_info.displayIndex - 1;

    let pager_idxs = [slider_idx];
    let prev_slides_num = slider_idx;
    let next_slides_num = slider_info.slideCount - slider_idx - 1;

    if (
      max_pagers_half <= prev_slides_num &&
      max_pagers_half <= next_slides_num
    ) {
      prev_slides_num = next_slides_num = max_pagers_half;
    } else if (max_pagers_half < prev_slides_num) {
      prev_slides_num = max_pagers_half + (max_pagers_half - next_slides_num);
    } else if (max_pagers_half < next_slides_num) {
      next_slides_num = max_pagers_half + (max_pagers_half - prev_slides_num);
    }

    let i;

    for (i = 1; i <= prev_slides_num; i++) {
      pager_idxs.push(slider_idx - i);
    }

    for (i = 1; i <= next_slides_num; i++) {
      pager_idxs.push(slider_idx + i);
    }

    pager_idxs.sort(function(a, b) {
      return a - b;
    });
  }
}
