import $ from 'jquery';
import {tns} from '../../node_modules/tiny-slider/src/tiny-slider';

export default class MilestonesSlider {
  constructor($wrapper) {
    this.$wrapper = $wrapper;
    this.$slider = $wrapper.find('.slideshow-gal');
    this.$pager = $wrapper.find('.slideshow-pager');

    this.initSlider();
    this.bindEvents();
  }

  initSlider() {
    const mode = 768 <= $(window).width() ? 'gallery' : 'carousel';

    this.slider = tns({
      container: this.$slider.get(0),
      items: 1,
      nav: false,
      lazyload: true,
      mode: mode,
      speed: 600,
      autoHeight: true
    });

    this.$slider.find('.tns-lazy-img').on('load', (e) => {
      const $img = $(e.currentTarget);
      $img.parent('.slideshow-img').css({
        backgroundImage: 'url(' + $img.attr('src') + ')'
      });
    });

    this.onSlideChange();
  }

  bindEvents() {
    this.$wrapper.find('.slideshow-pager .about-milestones-timeline').on('click', e => {
      e.preventDefault();
      const slide_idx = $(e.target).data('slide-idx');
      if (slide_idx || 0 === slide_idx) {
        this.slider.goTo(slide_idx);
      }
    });

    this.slider.events.on('indexChanged', this.onSlideChange.bind(this));
  }

  onSlideChange() {
    const slider_info = this.slider.getInfo();
    const $slides = this.$wrapper.find('.slideshow-slide');

    //const max_pagers = Math.min(slider_info.slideCount, 768 <= $(window).width() ? 5 : 3);
    //const max_pagers_half = Math.floor(max_pagers / 2);
    const slider_idx = slider_info.slideCount < slider_info.displayIndex ? 0 : slider_info.displayIndex - 1;

    let pager_idxs = [slider_idx];
    let prev_slides_num = slider_idx;
    let next_slides_num = slider_info.slideCount - slider_idx - 1;

    /*
    if (max_pagers_half <= prev_slides_num && max_pagers_half <= next_slides_num) {
      prev_slides_num = next_slides_num = max_pagers_half;
    } else if (max_pagers_half < prev_slides_num) {
      prev_slides_num = max_pagers_half + (max_pagers_half - next_slides_num);
    } else if (max_pagers_half < next_slides_num) {
      next_slides_num = max_pagers_half + (max_pagers_half - prev_slides_num);
    }
    */

    let i;

    for (i = 1; i <= prev_slides_num; i ++) {
      pager_idxs.push(slider_idx - i);
    }

    for (i = 1; i <= next_slides_num; i ++) {
      pager_idxs.push(slider_idx + i);
    }

    pager_idxs.sort(function (a, b) {
      return a - b;
    });

    console.log(pager_idxs);

    const pagers_html = pager_idxs.map((idx) => {
      const year = $slides.filter('.slideshow-slide-' + idx).eq(0).data('year');
      const li_class = (slider_idx === idx) ? ' class="active"' : '';
      return '<li' + li_class + '><a href="#" data-slide-idx="' + idx + '">' + year + '</a></li>';
    });

    this.$wrapper.find('.slideshow-pager .about-milestones-timeline').html(pagers_html.join(' '));
    this.$wrapper.find('.active')[0].scrollIntoView({behavior: 'smooth', block: 'nearest', inline: 'center'});
  }
}
