export const generateParams = params => {
  const kvp = document.location.search.substr(1).split('&');
  let i = kvp.length;
  let x;

  let new_params = {};

  while(i --) {
    x = kvp[i].split('=');

    if (2 === x.length) {
      new_params[x[0]] = x[1];
    }
  }

  for (let key in params) {
    const param_key = encodeURI(key);
    const param_value = encodeURI(params[key]);

    new_params[param_key] = param_value;
  }

  let new_search = '';
  let j = 0;

  for (let new_key in new_params) {
    if (0 < j) {
      new_search += "&";
    }

    new_search += new_key + "=" + new_params[new_key];
    j ++;
  }

  return new_search;
};

export const updateParams = (params) => {
  const new_search = generateParams(params);
  history.replaceState(null, null, location.pathname + "?" + new_search);
};

export const insertParams = params => {
  const new_search = generateParams(params);
  document.location.search = new_search;
};

export const readParam = key => {
  key = encodeURI(key);

  const kvp = document.location.search.substr(1).split('&');
  let i = kvp.length;
  let x;

  while(i --) {
    x = kvp[i].split('=');
    if (x[0] === key) {
      return decodeURI(x[1]);
    }
  }

  return null;
}
