import $ from 'jquery';

export default class Search {
    constructor($wrapper) {
        var _this = this;
        var closeText = $wrapper.data('close-text') ?? 'Back';
        this.$wrapper = $wrapper;
        this.$span = $('<span><svg xmlns="http://www.w3.org/2000/svg" x="0px" y="0px" width="13px" height="13px" viewBox="0 0 13 13"><path fill="none" stroke="currentColor" stroke-width="1" d="M0,6.5h10.9"/><polyline fill="none" stroke="currentColor" stroke-width="1" points="7.5,11.5 11.5,6.5 7.5,1.5 "/></svg>' + closeText + '</span>');

        this.$wrapper.find('button').on('click', function (ev) {
            ev.stopPropagation()
            if (_this.$wrapper.hasClass('open')) {
                // Submit the form
            } else {
                // Open the form
                ev.preventDefault()
                _this.$span.css({opacity: 0})
                _this.$wrapper.addClass('open').append(_this.$span)
                // Set the correct width of the input
                _this.$wrapper.find('input').css({
                    width: $('nav .menu-menu-container').width() + 20
                })
                _this.$wrapper.find('span').css({
                    left: -Math.abs($('nav .menu-menu-container').width() + 120)
                })
                setTimeout(() => {
                    _this.$span.animate({opacity: 1}, 200);
                    _this.$wrapper.find('input').focus();
                }, 300);
            }
        });

        this.$wrapper.find('input').on('click', function (ev) {
            ev.stopPropagation()
        });

        this.$wrapper.on('click', function (ev) {
            ev.stopPropagation()
            // Set the correct width of the input
            _this.$wrapper.find('input').css({
                width: 0
            })
            _this.$span.animate({opacity: 0}, 300, () => {
                _this.$wrapper.removeClass('open').find('span').remove();
            })
        });
    }
}
