import $ from "jquery";
import { tns } from "../../node_modules/tiny-slider/src/tiny-slider";

export default class GameSlideshow {
  constructor($wrapper) {
    this.autoplay_timeout = 5000;
    this.autoplay_interval = null;

    this.$wrapper = $wrapper;
    this.$slider = $wrapper.find(".slideshow-gal");
    this.$pager = $wrapper.find(".slideshow-pager");

    this.initSlider();
    this.bindEvents();
    this.startInterval();
  }

  initSlider() {
    const mode = 768 <= $(window).width() ? "gallery" : "carousel";

    this.slider = tns({
      container: this.$slider.get(0),
      items: 1,
      nav: false,
      controls: false,
      lazyload: true,
      mode: mode,
      speed: 600,
      animateIn: "fadeIn",
      animateOut: "fadeOut",
    });

    this.$slider.find(".tns-lazy-img").on("load", (e) => {
      const $img = $(e.currentTarget);
      $img.parent(".slideshow-img").css({
        backgroundImage: "url(" + $img.attr("src") + ")",
      });
    });

    this.onSlideChange();
  }

  bindEvents() {
    this.$wrapper.find(".slideshow-pager .col-2").on("click", (e) => {
      const idx = $(e.currentTarget).data("slide-idx");
      this.slider.goTo(idx);
    });

    this.slider.events.on("indexChanged", this.onSlideChange.bind(this));
  }

  startInterval() {
    clearTimeout(this.autoplay_interval);
    this.autoplay_interval = setTimeout(() => {
      this.slider.goTo("next");
    }, this.autoplay_timeout);
  }

  onSlideChange() {
    const slider_info = this.slider.getInfo();
    const $pagers = this.$wrapper.find(".slideshow-pager .col-2");
    const slider_idx =
      $pagers.length < slider_info.displayIndex
        ? 0
        : slider_info.displayIndex - 1;

    $pagers
      .removeClass("active")
      .eq(slider_idx)
      .addClass("active");

    this.startInterval();
  }
}
