import $ from 'jquery';

export default class MediaTeaser {
  constructor($wrapper) {
    this.$wrapper = $wrapper;

    if ($wrapper.find('.media-teaser-video').length) {
      this.initVideo();
    }
  }

  initVideo() {
    this.$video_container = this.$wrapper.find('.media-teaser-video');
    this.$player = this.$video_container.children('div').eq(0);
    this.video = this.$video_container.data('video');
    this.poster = this.$video_container.data('poster');

    this.player = window.jwplayer(this.$player.get(0));

    this.player.setup({
      playlist: [{
        file: this.video,
        image: this.poster
      }]
    });

    this.player.on('meta', (m) => {
      const $p_container = $(this.player.getContainer()).parent();

      $p_container.data({
        width: m.width,
        height: m.height
      });

      this.setPlayerRatio($p_container);
    });

    let resize_timer = null;
    $(window).on('resize', () => {
      clearTimeout(resize_timer);
      resize_timer = setTimeout(this.resizeNow.bind(this), 300);
    });

    this.resizeNow();
  }

  setPlayerRatio($player_container) {
    const video_h = parseFloat($player_container.data('height')),
      video_w = parseFloat($player_container.data('width')),
      video_r = video_h / video_w,
      player_w = 16,
      player_h = 9,
      win_w = this.$wrapper.width(),
      win_h = this.$wrapper.height();

    if (!video_w || !video_h) {
      return;
    }

    let container_w = 0,
      container_h = 0;

    /**
     * Fit player aspect to video file aspect
     */
    $player_container.find('.jwplayer .jw-aspect').css('padding-top', Math.round(video_r * 10000) / 100 + "%");

    if (video_r < win_h / win_w) {
      $player_container.removeClass('video-width');
      container_h = win_h;
      container_w = win_h / video_r;
    } else {
      $player_container.addClass('video-width');
      container_w = win_w;
      container_h = win_w * video_r;
    }

    $player_container.css({
      width: container_w,
      height: container_h
    });

    $player_container.children('.jwplayer').trigger('resize');
  }

  resizeNow() {
    this.$video_container.css({
      width: 1.78 * this.$wrapper.height()
    });
    this.setPlayerRatio($(this.player.getContainer()).parent());
  }
}