  $(".js-single-slideshow").each(function() {
    const $container = $(this);
    const $indicator = $(this).find(".js-slideshow-indicator");
    const $current = $(this).find(".js-current-image");
    const $next = $(this).find(".js-next-image");
    const slides = $indicator.attr("data-images").split(",");
    const interval = $container.data('slideshow-interval') ? parseInt($container.data('slideshow-interval')) : 5000;
    let currentSlide = 0;
    let slideInterval = null;
    let slideChangeTimeout = null;

    $current.css("background-image", `url(${slides[0]})`);

    const updateSlide = (index) => {
      if (slideChangeTimeout) {
        clearTimeout(slideChangeTimeout);
      }

      $next
        .css("background-image", "url(" + slides[index] + ")")
        .css("opacity", 1);

      $current
        .css("background-image", "url(" + slides[index] + ")")
        .css("opacity", 1);

      $next.css("opacity", 0);
    };

    const startInterval = () => {
      $indicator.addClass("active");
      slideInterval = setInterval(() => {
        currentSlide++;
        if (currentSlide >= slides.length) {
          currentSlide = 0;
        }

        updateSlide(currentSlide);
      }, interval);
    };

    startInterval();

    $(this)
      .find(".js-slide-next")
      .click((e) => {
        e.preventDefault();

        clearInterval(slideInterval);

        currentSlide++;
        if (currentSlide >= slides.length) {
          currentSlide = 0;
        }

        updateSlide(currentSlide);

        startInterval();
        $indicator.removeClass("active");
        setTimeout(() => $indicator.addClass("active"), 0);
      });

    $(this)
      .find(".js-slide-prev")
      .click((e) => {
        e.preventDefault();

        clearInterval(slideInterval);

        currentSlide--;
        if (currentSlide < 0) {
          currentSlide = slides.length - 1;
        }

        updateSlide(currentSlide);

        startInterval();
        $indicator.removeClass("active");
        setTimeout(() => $indicator.addClass("active"), 0);
      });
  });

  $(".js-game-slideshow").each(function() {
    new GameSlideshow($(this));
  });

