$(".hero-scroll-down-container").click(function() {
  const $hero = $(this)
    .parents(".hero, .press-hero, .js-game-slideshow-stage")
    .eq(0);

  $("html,body").animate(
    { scrollTop: $hero.outerHeight() },
    { duration: "slow" }
  );
});

let lastScrollTop = 0;

$(window).scroll(function(event) {
  var st = $(this).scrollTop();
  if (st > lastScrollTop) {
    $("header").addClass("hidden");
  } else {
    $("header").removeClass("hidden");

    if (st <= 5) {
      if ($("header").attr("data-inverted")) {
        $("header").removeClass("bg");
      } else {
        $("header").removeClass("inverted bg");
      }
    } else {
      $("header").addClass("inverted bg");
    }
  }

  if (st < 0) {
    st = 0;
  }
  lastScrollTop = st;
});

let menuOpen = false;

for (let toggle of document.getElementsByClassName("js-toggle-menu")) {
  toggle.addEventListener("click", function(event) {
    event.preventDefault();

    menuOpen = !menuOpen;

    document.body.classList[menuOpen ? "add" : "remove"]("no-overflow");
    document
      .getElementById("nav")
      .classList[menuOpen ? "add" : "remove"]("open");
    for (let t of document.getElementsByClassName("js-toggle-menu")) {
      t.classList[menuOpen ? "add" : "remove"]("open");
    }
  });
}

const mobileNav = document.getElementById("mobile-nav-main");

if (mobileNav) {
    try {
        mobileNav.querySelectorAll(".menu-item-has-children").forEach((accordion) => {
            let portfolioSectionOpen = false;
            const toggler = accordion.getElementsByTagName("a")[0];
            const arrow = accordion.querySelector('.menu-item-toggle');
            const subMenu = accordion.querySelector(".sub-menu");
            arrow.addEventListener("click", (e) => {
                portfolioSectionOpen = !portfolioSectionOpen;
                subMenu.classList[portfolioSectionOpen ? "add" : "remove"]("open");
            });
            toggler.addEventListener("click", (e) => {
                if (!subMenu.classList.contains('open')) {
                    e.preventDefault();
                    portfolioSectionOpen = !portfolioSectionOpen;
                    subMenu.classList[portfolioSectionOpen ? "add" : "remove"]("open");
                }
            });
        });
    } catch (e) {
        console.error(e);
    }
}

