let togglerText = "Mehr Anzeigen";
if ($("html").attr("lang") == "en-US") {
  togglerText = "Show more";
}

let toggledText = "Weniger Anzeigen";
if ($("html").attr("lang") == "en-US") {
  toggledText = "Show less";
}
$(document).on("click", "p.toggle-filter", function() {
  var buttonText = "";
  if ($(this).hasClass("expanded")) {
    buttonText = togglerText;
  } else {
    buttonText = toggledText;
  }
  $(this).html(buttonText);
  $(this).toggleClass("expanded");
  $(this)
    .siblings("ul.filter-tags")
    .toggleClass("expanded");
});

if (
  $(".filter-tags").length &&
  $("form .filter-tags").prop("scrollHeight") > 100
) {
  $("form .filter-tags")
    .parent()
    .append("<p class='button dark toggle-filter'>" + togglerText + "</p>");
}

$(".menu-item-has-children", $menu).each(function() {
  const $parent = $(this);
  const $subMenu = $(".sub-menu", $parent).first();
  $parent.hover(
    (e) => {
      e.preventDefault();
      $subMenu.css("visibility", "hidden").show();
      const subMenuBox = $subMenu[0].getBoundingClientRect();
      const subMenuData = {
        top: subMenuBox.y,
        height: subMenuBox.height,
      };
      $subMenu
        .css("visibility", "visible")
        .hide()
        .stop(true, true)
        .fadeIn();
      $background
        .css({
          top: subMenuData.top,
          height: subMenuData.height,
          position: "absolute",
        })
        .stop(true, true)
        .fadeIn();
    },
    () => {
      $background.stop(true, true).fadeOut();
      $subMenu.stop(true, true).fadeOut();
    }
  );
});
