import {tns} from '../../node_modules/tiny-slider/src/tiny-slider';

const selectors = {
    carousel: 'data-posts-slider-carousel',
};

export default class PostsSlider {
    constructor(element) {
        this.container = element;
        this.carousel = element.querySelector(`[${selectors.carousel}]`);

        this.initSlider();
    }

    initSlider() {
        let options = this.carousel.getAttribute(selectors.carousel);
        let config = {
            container: this.carousel,
            mode: 'carousel',
            fixedWidth: 280,
            gutter: 20,
            nav: false,
            controls: true,
            slideBy: 'page',
            speed: 600,
            loop: false,
            mouseDrag: true,
            responsive: {
                1024: {
                    fixedWidth: 320,
                    gutter: 30,
                },
            }
        };

        if (options) {
            config = Object.assign({}, config, JSON.parse(options));
        }

        this.slider = tns(config);
    }
}
