import SimpleBar from 'simplebar';

const JobListing = ($) => {
  const $allJobs = $('[data-job]');
  const $allListings = $('[data-listing]');

  if ($allJobs.length && $allListings.length) {
    let current_job = null;

    const $wrapper = $('.jobs-vacancies[data-default-location]');
    const default_location = $wrapper.length ? $wrapper.data('default-location') : false;

    const getCurrentJobDetail = () => {
      return $allListings.filter('[data-listing="' + current_job + '"]');
    };

    const resizeJobList = () => {
      const $job_detail = getCurrentJobDetail();
      $('.jobs-shortlist').css('max-height', $job_detail.outerHeight());
    };

    const switchJobDetail = (listing_id) => {
      if (listing_id === current_job) {
        return;
      }

      current_job = listing_id;

      $allListings.hide();
      getCurrentJobDetail().show();

      $allJobs.removeClass('active-job');
      $allJobs.filter('[data-job="' + current_job + '"]').addClass('active-job');

      resizeJobList();
    };

    const disableNonResultFilter = () => {
      const $area_filter = $('[data-filter="area"]');
      const $type_filter = $('[data-filter="type"]');
      const $experience_filter = $('[data-filter="experience"]');

      const area_filter_val = $area_filter.val()
      const type_filter_val = $type_filter.val();
      const experience_filter_val = $experience_filter.val();

      const base_selector_area = area_filter_val === '' ? '' : '[data-function="' + area_filter_val + '"]';
      const base_selector_type = type_filter_val === '' ? '' : '[data-type="' + type_filter_val + '"]';
      const base_selector_experience = experience_filter_val === '' ? '' : '[data-experience-level="' + experience_filter_val + '"]';

      const $area_options = $area_filter.children('option');
      const $type_options = $type_filter.children('option');
      const $experience_options = $experience_filter.children('option');

      $area_options.attr('disabled', false);
      $area_options.each(function (i) {
        if (0 === i) {
          return;
        }

        const selector = base_selector_type + base_selector_experience + '[data-function="' + $(this).text() + '"]';
        if (!$(selector).length) {
          $(this).attr('disabled', true);
        }
      });

      $type_options.attr('disabled', false);
      $type_options.each(function (i) {
        if (0 === i) {
          return;
        }

        const selector = base_selector_area + base_selector_experience + '[data-type="' + $(this).text() + '"]';
        if (!$(selector).length) {
          $(this).attr('disabled', true);
        }
      });

      $experience_options.attr('disabled', false);
      $('[data-filter="experience"] option').each(function (i) {
        if (0 === i) {
          return;
        }

        const selector = base_selector_area + base_selector_type + '[data-experience-level="' + $(this).text() + '"]';
        if (!$(selector).length) {
          $(this).attr('disabled', true);
        }
      });
    };

    const updateJobCount = () => {
      disableNonResultFilter();
      $('[data-job-count]').text($allJobs.filter(':visible').length);
    };

    const scrollTopJobOffers = () => {
      $([document.documentElement, document.body]).animate({
        scrollTop: $('.jobs-vacancies').offset().top
      }, 500);
    };

    // Event bindings
    $allJobs.on('click', (e) => {
      e.preventDefault();

      const listing_id = $(e.currentTarget).data('job')

      switchJobDetail(listing_id);

      $([document.documentElement, document.body]).animate({
        scrollTop: $('[data-listing=' + listing_id + ']').offset().top - 150
      }, 500);
    });

    $('.search-box-form').on('submit', (e) => {
      e.preventDefault();

      const $target = $(e.currentTarget);

      if ($target.find('input').val() !== '') {
        // Reset any previous filters
        $('[data-filter]').prop('selectedIndex', 0).trigger('change');
        $allJobs.hide();
        $('[data-title*="' + $target.find('input').val() + '" i]').show();
      } else {
        $allJobs.show();
      }

      updateJobCount();
      scrollTopJobOffers();
    });

    $('[data-location-filter]').on('click', (e) => {
      e.preventDefault();
      e.stopPropagation();

      const $target = $(e.currentTarget);

      $('[data-location-filter]').addClass('disabled').removeClass('active-filter');
      $target.removeClass('disabled').addClass('active-filter');

      var location = $target.attr('data-location-filter');

      setTimeout(function () {
        $allJobs.hide();
        $('[data-location="' + location + '"]').show();
        switchJobDetail($('[data-location="' + location + '"]').eq(0).data('job'));
        updateJobCount();
      }, 500)

      scrollTopJobOffers();
    });

    $('[data-filter]').on('change', (e) => {
      e.preventDefault();

      $allJobs.hide();

      let criteria = '';

      if ($('[data-filter="area"]').val() !== '') {
        criteria += '[data-function="' + $('[data-filter="area"]').val() + '"]';
      }

      if ($('[data-filter="type"]').val() !== '') {
        criteria += '[data-type="' + $('[data-filter="type"]').val() + '"]';
      }

      if ($('[data-filter="experience"]').val() !== '') {
        criteria += '[data-experience-level="' + $('[data-filter="experience"]').val() + '"]';
      }

      if (criteria === '') {
        criteria = '[data-job]';
      }

      $(criteria).show();

      updateJobCount();
    });

    let resize_timer = null;

    $(window).on('reisze', () => {
      clearTimeout(resize_timer);
      resize_timer = setTimeout(resizeJobList, 500);
    });

    // Init scrollbar
    new SimpleBar($('.jobs-shortlist')[0], {
      autoHide: false
    });

    switchJobDetail($allListings.first().data('listing'));

    if (default_location) {
      $('[data-location-filter]').addClass('disabled').removeClass('active-filter');
      $('[data-location-filter="' + default_location + '"]').removeClass('disabled').addClass('active-filter');
      $allJobs.hide();
      $('[data-location="' + default_location + '"]').show();
      switchJobDetail($('[data-location="' + default_location + '"]').eq(0).data('job'));
    }

    updateJobCount();
  }
};

export default JobListing;