import $ from "jquery";
import 'bootstrap/js/dist/tooltip';

const selectors = {
    buttonCopy: 'data-sharing-copy',
};

export default class Sharing {
    constructor (element) {
        this.element = element;
        this.buttonCopy = element.querySelector(`[${selectors.buttonCopy}]`);

        if (this.buttonCopy) {
            this.buttonCopy.addEventListener('click', this.copy.bind(this));
        }
    }

    async copy () {
        const link = this.buttonCopy.getAttribute(selectors.buttonCopy);
        const tooltip = this.buttonCopy.getAttribute('data-title');

        try {
            await navigator.clipboard.writeText(link);
            console.log('copied');
            this.buttonCopy.setAttribute('title', tooltip);
            $(this.buttonCopy).tooltip('show');
        } catch (error) {
            console.error(error.message);
        }
    }
}
