import $ from 'jquery';
import 'bootstrap-daterangepicker';
import moment from 'moment/moment'
import {insertParams, readParam, updateParams} from './utils';

export default class PressFilter {
  constructor() {
    this.$category_filter = $('#blog-category-filter');
    this.$daterange_filter = $('#blog-daterange-filter');
    this.loading = false;
    this.lmpage = readParam('lmpage');

    if (!this.lmpage) {
      this.lmpage = 1;
    }

    if (this.$category_filter.length && this.$daterange_filter.length) {
      this.initFilters();
      this.bindEvents();
    }
  }

  initFilters() {
    // Init daterange filter
    let picker_options = {
      autoUpdateInput: false,
      locale: {
        cancelLabel: window.wp_localized.clear,
        applyLabel: window.wp_localized.apply
      }
    };

    let daterange = readParam('datespan');
    let has_daterange = false;

    if (daterange && '' !== daterange) {
      daterange = daterange.split('t');
      if (2 === daterange.length) {
        has_daterange = true;
        picker_options.startDate = moment(daterange[0]);
        picker_options.endDate = moment(daterange[1]);
      }
    }

    this.$daterange_filter.daterangepicker(picker_options);

    if (has_daterange) {
      const start_date = this.$daterange_filter.data('daterangepicker').startDate.format('DD/MM/YYYY');
      const end_date = this.$daterange_filter.data('daterangepicker').endDate.format('DD/MM/YYYY');

      this.$daterange_filter.val(start_date + ' - ' + end_date);
    }

    // Init relevance filter
    const relevance = readParam('relevance');
    let has_relevance = false;
    if (relevance && '' !== relevance) {
      has_relevance = true;
      this.$category_filter.val(relevance);
    }

    if (has_relevance || has_daterange) {
      location.hash = '#press-archive-main';
    }
  }

  bindEvents() {
    // Relevance filter
    this.$category_filter.on('change', () => {
      const val = $('#blog-category-filter').val();
      insertParams({
        relevance: val,
        lmpage: 0
      });
    });

    // Daterange filter
    this.$daterange_filter.on('apply.daterangepicker', function(ev, picker) {
      $(this).val(picker.startDate.format('DD/MM/YYYY') + ' - ' + picker.endDate.format('DD/MM/YYYY'));
      insertParams({
        datespan: picker.startDate.format('YYYY-MM-DD') + 't' + picker.endDate.format('YYYY-MM-DD'),
        lmpage: 0
      });
    });

    this.$daterange_filter.on('cancel.daterangepicker', function(ev, picker) {
      insertParams({
        datespan: '',
        lmpage: 0
      });
    });

    // Load more
    $('.archive-more-row .button').on('click', e => {
      e.preventDefault();

      if (this.loading) {
        return false;
      }

      this.loading = true;
      $('.post-listing-row').addClass('loading');

      const next_page_url = $(e.currentTarget).attr('href');

      $.ajax({
        url: next_page_url,
        type: "GET",
        dataType: "html"
      })
        .done(function (data) {
          const $data = $(data);
          $('.post-listing-row').append($data.find('.post-listing-row').children());

          const $more_btn = $data.find('.archive-more-row .button');
          if ($more_btn.length) {
            $('.archive-more-row .button').attr('href', $more_btn.eq(0).attr('href'));
          } else {
            $('.archive-more-row').remove();
          }
        })
        .always(() => {
          this.loading = false;
          $('.post-listing-row').removeClass('loading');

          this.lmpage ++;
          updateParams({lmpage: this.lmpage});
        });
    });
  }
}