import $ from 'jquery';

export default class RelatedPress {
  constructor($wrapper) {
    this.$wrapper = $wrapper;
    this.current_page = 1;
    this.post_exclude = $wrapper.data('post-exclude');
    this.post_cat = $wrapper.data('post-cat');
    this.loading = false;
    this.$listing_row = $wrapper.find('.post-listing-row');
    this.bindEvents();
    this.loadPosts();
  }

  bindEvents() {
    this.$wrapper.find('.archive-more-row .button').on('click', (e) => {
      e.preventDefault();

      if (this.loading) {
        return false;
      }

      this.current_page ++;
      this.loadPosts();
    });
  }

  loadPosts() {
    this.loading = true;
    this.$listing_row.addClass('loading');

    $.getJSON(
      window.wp_localized.api_url,
      {
        action: 'get-related-press-api',
        page: this.current_page,
        exclude: this.post_exclude,
        cat: this.post_cat,
        lang: window.wp_localized.lang
      },
      (data) => {
        this.loading = false;
        this.$listing_row.removeClass('loading');

        const len = data.length;
        const post_len = Math.min(len, 4);
        let posts_html = [];

        for (let i = 0; i < post_len; i ++) {
          console.log('data[i]', data[i])
          var image = data[i].image ? data[i].image : '/content/themes/ubisoft/images/related-posts-placeholder.png'

          var categories = ''

          var maxCategories = 2
          var currentCategoryCount = 0

          $.each(data[i].categories, function (index, category) {
            if (currentCategoryCount < maxCategories) {
              if (categories == '') {
                categories += '' + category
              } else {
                categories += ', ' + category
              }
              currentCategoryCount++;
            }
          })
          
          posts_html.push(
            '<a href="' + data[i].link + '" class="post-listing-item">' +
              '<div class="post-listing-image"><img src="' + image + '"/></div>' +
              '<div class="post-listing-copy">' +
                '<div class="post-listing-title">' + data[i].title + '</div>' +
                '<div class="meta"><span>' + data[i].dateLocal + '</span><span><em>' + categories + '</em></span></div>' + 
              '</div>' +
            '</a>'
          );
        }

        this.$listing_row.append(posts_html.join(' '));

        if (5 > len) {
          this.$wrapper.find('.archive-more-row').remove();
        }
      }
    );
  }
}