import $ from 'jquery';

export default class TextPreview {
  constructor($wrapper) {

    var _this = this;

    this.$wrapper = $wrapper;

    this.$before = $wrapper.find('[data-state="before"]');
    this.$after = $wrapper.find('[data-state="after"]');
    
    let buttonText = $wrapper.attr('data-button-text') ? $wrapper.attr('data-button-text') : 'Read more';
    this.$button = $('<a href="#" class="button dark">' + buttonText + '</a>');

    // Only hide and show button IF the "after" content exists. If not, there is no need for the button.
    if (this.$after.html() !== '') {
        this.$after.after(this.$button).hide();
        this.$wrapper.addClass('transformed');
    }

    this.$button.on('click', function(ev) {
      ev.preventDefault()

      _this.$after.show();
      _this.$button.hide();
    });
  }
}