import $ from 'jquery';

export default class HeaderVideoBackground {
  constructor($wrapper) {
    this.$hero = $wrapper.parents('.hero').eq(0);
    this.$wrapper = $wrapper;
    this.$player = $wrapper.children('div').eq(0);
    this.video = $wrapper.data('video');
    this.poster = $wrapper.data('poster');

    if ($(window).width() <= 768) {
      $wrapper.remove();
      return;
    }

    this.initVideo();
  }

  initVideo() {
    this.player = window.jwplayer(this.$player.get(0));

    this.player.setup({
      playlist: [{
        file: this.video,
        image: this.poster
      }],
      autostart: false,
      mute: true,
      repeat: false,
      stretching: 'fill',
      controls: false,
      preload: 'auto',
      width: '100%',
      height: '100%'
    });

    this.player.on('ready', () => {
      this.player.play();
    });

    this.player.on('time', (e) => {
      if ((e.position + 0.4) >= e.duration) {
        this.player.seek(0);
      }
      /*if (e.position >= (e.duration - 2) && !isReady) {
        isReady = true;
        videoContainer.addClass('is-fading');
      }*/
    });
  }
}