import $ from "jquery";
import 'bootstrap/js/dist/modal';

const selectors = {
    button: '[data-video-modal-button]',
};

export default class VideoModal {
    constructor (element) {
        this.modal = element;
        this.button = element.parentNode.querySelector(selectors.button);
        this.video = element.querySelector(`iframe`);

        if (this.video && this.button) {
            this.videoUrl = this.video.getAttribute('data-src');
            const _this = this;

            $(this.button).modal({
                'show': false,
            });

            this.button.addEventListener('click', () => {
                this.video.setAttribute('src', this.videoUrl + '?autoplay=1&amp;modestbranding=1&amp;showinfo=0');
            });
        }

        $(this.modal).on('hide.bs.modal', function (e) {
            const video = element.querySelector(`iframe`);
            if (video) {
                video.setAttribute('src', '');
            }
        });

    }
}
