import $ from 'jquery';

export default class Uplay {
  constructor($wrapper) {
    this.$wrapper = $wrapper;

    this.myAppId = '15747eb3-e910-4626-88f1-8b53dbd3ef54';
    this.myGenomeId = '283fee7b-9d69-470b-9393-4dfe1a718278';
    this.nextUrl = window.location.hostname;
    this.frameRegistration = document.getElementById('frameRegistration');
    this.connectSDK = null
    this.nav = document.querySelector('.nav-side')
    this.loginBtn = document.querySelectorAll('[data-button="login"]')
    this.logoutBtn = document.querySelectorAll('[data-button="logout"]')
    this.tdata = null
    this.environment = 'PROD'

    let _this = this


    if(window.location.hostname == 'staging.anno-union.com' && typeof document.querySelector("iframe#frameRegistration") != "undefined"
    ){
      if(document.querySelector("iframe#frameRegistration") && document.querySelector("iframe#frameRegistration").src){
        var oldSrc = document.querySelector("iframe#frameRegistration").src
        var newSrc = oldSrc.replace("anno-union.com","staging.anno-union.com")
        document.querySelector("iframe#frameRegistration").src = newSrc
      }
    }

    Connect.init({
      env: _this.environment,
      appId: _this.myAppId,
      genomeId: _this.myGenomeId,
      lang: 'en-US',
      nextUrl: encodeURIComponent(_this.nextUrl),  // This parameter is only used with the thirdPartyCookiesSupport flow
      thirdPartyCookiesSupport: false
    });

    if ($(_this.logoutBtn).length) {
      $(_this.logoutBtn).on('click', function (e) {
        e.preventDefault();

        _this.connectSDK.logout(_this.tdata.ticket, _this.tdata.sessionId).subscribe()
        setTimeout(() => {
          parent.location.href = "/wp-logout.php";
        }, 1000)
      })
    }

    console.log('connectSDK prep 0 0')

    Connect.sdk.subscribe(function(sdk) {
      console.log('connectSDK sdk 1', sdk)

      _this.connectSDK = sdk; // It is needed to save the sdk as a variable if you need to call the sdk later outside of this callback

      _this.connectSDK.getTicket().subscribe(function(obj) {

        console.log('connectSDK subscribe', obj);

        if (obj.status == 'FAILED') {

          if ($(_this.logoutBtn).length && $(_this.loginBtn).length) {
            $(_this.logoutBtn).hide()
            $(_this.loginBtn).show()
          }

        } else {

          var tdata = obj.payload;
          _this.tdata = tdata

          if ($(_this.logoutBtn).length && $(_this.loginBtn).length) {
            if (tdata) {
              $(_this.logoutBtn).show()
              $(_this.loginBtn).hide()
            } else {
              $(_this.logoutBtn).hide()
              $(_this.loginBtn).show()
            }
            $(_this.logoutBtn).data('ubi', tdata.ticket);
          }

          _this.connectSDK.getProfiles(tdata.ticket, tdata.sessionId, tdata.userId).subscribe(function(profile) {

            profile = profile.payload.profiles[0]

            $.ajax({
              url: "/wp-setupuplay.php",
              method: "POST",
              dataType: 'json',
              data: {
                status: obj.status,
                username: profile.nameOnPlatform,
                ticket: obj.ticket,
                user_id: profile.userId,
                sessionId: obj.sessionId
              },
              success: function (result) {
                window.uPlayData = true;
              },
              error: function (error) {
                window.uPlayData = null;
              }
            });

            if (frameRegistration !== undefined && frameRegistration !== null) {
              frameRegistration.innerHTML = '<button type="button" class="vc_gitem-link vc_general vc_btn3 vc_btn3-size-md vc_btn3-shape-rounded vc_btn3-style-flat vc_btn3-color-juicy-pink ubisoft-logout" style="margin: 0 auto;"> Logout </button>';
              $(_this.logoutBtn).data('ubi', obj.ticket);
            }
          });
        }
      });
    });
  }
}