import $ from "jquery";

export default class MilestonesSlider {
  constructor($wrapper) {
    this.$wrapper = $wrapper;

    this.$loadMoreCTA = $wrapper.find("[data-filter-load-more]");

    let $inputType = $wrapper.find("[name=filter-type]");
    let $inputDate = $wrapper.find("[name=filter-date]");

    let $tagList = $wrapper.find("form .filter-tags");

    this.activeTags = [];

    let _this = this;

    $tagList.children("li").each(function() {
      $(this).on("click", function(ev) {
        ev.preventDefault();
        let currentTags = _this.activeTags;
        if ($(this).hasClass("active")) {
          $(this).removeClass("active");
          // remove from active tag list and re-filter
          let newTags = [];
          currentTags.forEach((tag) => {
            if (tag != $(this).attr("data-tag")) {
              newTags.push(tag);
            }
          });
          _this.activeTags = newTags;
          _this.filter(_this.type, _this.date, 9);
        } else {
          $(this).addClass("active");
          // add to active tag list and re-filter
          let newTags = currentTags;
          newTags.push($(this).attr("data-tag"));
          _this.activeTags = newTags;
          _this.filter(_this.type, _this.date, 9);
        }
      });
    });

    this.type = null;
    this.date = null;
    this.count = 9;

    // Show first 9 by default
    this.filter(null, null, 9);

    $inputType.on("change", function(ev) {
      if (ev.target.value && ev.target.value != "") {
        _this.filter(ev.target.value, _this.date, 9);
      } else {
        _this.filter(null, $inputDate.val(), 9);
      }
    });
    $inputDate.on("change", function(ev) {
      _this.filter(_this.type, ev.target.value, 9);
    });

    this.$loadMoreCTA.on("click", function(ev) {
      ev.preventDefault();

      _this.filter(_this.type, _this.date, _this.count + 9);
    });
  }
  filter(type, date, count) {
    let _this = this;

    console.log("filter args", type, date, count, _this.activeTags);

    this.type = type;
    this.date = date;
    this.count = count;

    // Hide any previous results
    this.$wrapper.find(".section-post-filter-item").hide();

    let $allPosts = this.$wrapper.find(
      "[data-markup], .section-post-filter-item"
    );

    let shownCount = 0;
    let totalCount = 0;

    $allPosts.each(function(index, item) {
      let show = false;

      // No filter - show all
      if (!type && !date && _this.activeTags.length == 0) {
        show = true;
      }

      // Filter by type
      if (type) {
        if (type == $(item).attr("data-type")) {
          show = true;
        }
      }

      // Filter by tag
      if (_this.activeTags.length > 0) {
        let _show = true;
        // Check if this post has at least one associated tag
        let postTags = $(item).attr("data-tags")
          ? $(item)
              .attr("data-tags")
              .split(",")
          : [];
        console.log("compare lists", $(this), postTags, _this.activeTags);

        // Check if all postTags are in activeTags
        _this.activeTags.every(tag => {
          if (!postTags.includes(tag)) {
            _show = false;
            return false;
          }
          return true;
        });

        // Check if we are also filtering by type and do another check.
        if (type && show && _show) {
          console.log("check - a", type, show, _show);
          show = true;
        } else if (type && !show) {
          console.log("check - b", type, show, _show);
          show = false;
        } else if (!type && _show) {
          console.log("check - c", type, show, _show);
          show = true;
        } else {
          show = false;
        }
      }

      if (show) {
        totalCount++;
        if (shownCount < count) {
          if (
            $(item).attr("data-embed") &&
            isValidJSONString($(item).attr("data-markup"))
          ) {
            $(item).replaceWith(
              '<div class="section-post-filter-item" data-type="' +
                $(item).attr("data-type") +
                '" data-tags="' +
                $(item).attr("data-tags") +
                '" data-year="' +
                $(item).attr("data-year") +
                '" data-month="' +
                $(item).attr("data-month") +
                '" data-day="' +
                $(item).attr("data-day") +
                '" data-id="' +
                $(item).attr("data-id") +
                '">' +
                $(item)
                  .html(JSON.parse($(item).attr("data-embed")))
                  .text() +
                JSON.parse($(item).attr("data-markup")) +
                "</div>"
            );
          } else if (
            $(item).attr("data-markup") &&
            isValidJSONString($(item).attr("data-markup"))
          ) {
            // not-yet-rendered
            $(item).replaceWith(
              '<div class="section-post-filter-item" data-type="' +
                $(item).attr("data-type") +
                '" data-tags="' +
                $(item).attr("data-tags") +
                '" data-year="' +
                $(item).attr("data-year") +
                '" data-month="' +
                $(item).attr("data-month") +
                '" data-day="' +
                $(item).attr("data-day") +
                '" data-id="' +
                $(item).attr("data-id") +
                '">' +
                JSON.parse($(item).attr("data-markup")) +
                "</div>"
            );
          } else {
            $(item).show();
          }
          shownCount++;
        }
      }
    });

    if (shownCount < totalCount) {
      this.$loadMoreCTA.show();
    } else {
      this.$loadMoreCTA.hide();
    }
  }
}

function isValidJSONString(str) {
  try {
    JSON.parse(str);
  } catch (e) {
    return false;
  }
  return true;
}
