import $ from 'jquery';

export default class SectionListTitle {
  constructor($wrapper) {
    this.$wrapper = $wrapper;
    this.$titles = $wrapper.find('h2');

    let resize_timer = null;

    $(window).on('resize', () => {
      clearTimeout(resize_timer);
      resize_timer = setTimeout(this.resizeNow.bind(this), 300);
      this.$titles.css('height', 'auto');
    });

    this.resizeNow();
  }

  resizeNow() {
    let max_h = 0;
    this.$titles.each(function() {
      max_h = Math.max($(this).height(), max_h);
    });
    this.$titles.css('height', max_h);
  }
}